* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}

section {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    flex-direction: column;
}

body,
html,
#root {
    height: 100%;
}

.guess-color {
    height: 275px;
    width: 425px;
    margin-bottom: 3.5vh;
}

.buttons-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.answear-button {
    padding: 17.5px;
    font-weight: 600;
    text-transform: uppercase;
    border: 1px solid royalblue;
    border-radius: 2.5px;
}

.wrong-answear,
.correct-answear {
    text-align: center;
    margin-top: 27.5px;
    font-size: 1.5rem;
    text-transform: uppercase;
    font-weight: bold;
}

.generator-info {
    text-align: center;
    margin-top: 25px;
    font-size: 1rem;
    color: darkslategray;
    font-weight: normal;
}

.correct-answear {
    color: green;
}

.wrong-answear {
    color: red;
}

section h1 {
    display: flex;
    text-transform: uppercase;
    margin-bottom: 7.5vh;
    font-size: 1.75rem;
}